<template>
    <div>
        <h1>
          PROPS Number:
          {{value}}
        </h1>

        <h3>
          data() num:
          {{num}}
        </h3>

        <button v-on:click="plusOne()">+</button>
        <button v-on:click="minusOne()">-</button>

    </div>
</template>

<script>
var data = {num:0}

export default {
  created: function(){
      console.log("Counter Components");
      this.countSec();
  },
  name: 'Counter',
  props: {
    value: "",
  },
  data(){
    return data;
  },
  methods:{
    plusOne: function(){
      this.num++;
    },
    minusOne: function(){
      this.num--;
    },
    countSec: function(){
      //setInterval(()=> {this.plusOne},1000);
    }

  }

}
</script>